import React, { useState } from 'react';
import { Carousel } from 'react-bootstrap';

import './StorySlider.css';

import story1 from './../img/story1.svg';
import story2 from './../img/story2.svg';
import story3 from './../img/story3.svg';
import story4 from './../img/story4.svg';
import story5 from './../img/story5.svg';
import story6 from './../img/story6.svg';
import story7 from './../img/story7.svg';
import story8 from './../img/story8.svg';

const images = [
  story1, story2, story3, story4, story5, story6, story7, story8
];


const carouselStyle = {
 
};

function StorySlider() {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };

  return (
    <Carousel style={carouselStyle} activeIndex={index} onSelect={handleSelect}>
      { images.map( img => {
        return <Carousel.Item key={img}>
          <img
          className="story-image"
          src={ img }
          alt={ "story " + img }
        />
        </Carousel.Item>
      } ) }
    </Carousel>
  );
};

export default StorySlider;