import React, { Component } from 'react';
import { Marker, Popup } from 'react-map-gl';

import './PinMarker.css';

import Icon from './../img/marker.svg';

// in pixels
const IconWidth = 60;
const IconHeight = 30;

class PinMarker extends Component {
  state = {
    selectedPin: null
  }

  render() {
    const longitude = this.props.trip.coordinates[1];
    const latitude = this.props.trip.coordinates[0];
    const location = this.props.trip.location;
    const message = this.props.trip.impression;
    return(
      <div>
        <Marker key={ `marker-${this.props.index}` }
          longitude={ longitude } latitude={ latitude }
          offsetLeft={ - IconWidth / 2 } offsetTop={ - IconHeight } >
          <img className="pin-marker" src={ Icon } alt="marker-icon"
            width={ `${IconWidth}px` } height={ `${IconHeight}` }
            onClick={ e => {
              this.setState({ selectedPin: { longitude: longitude, latitude: latitude } });
            }} />
        </Marker>
          { this.state.selectedPin && (
            <Popup latitude={ this.state.selectedPin.latitude } longitude={ this.state.selectedPin.longitude }
              onClose={() => { this.setState({ selectedPin: null }); }} >
              <div>
                <h5>{ location }</h5>
                <div>{ message }</div>
              </div>
            </Popup>
          ) }
       </div>
      );
  }
};

export default PinMarker;